const fadeIn = keyframes`
  from { opacity: 0; }
  to { opacity: 1; }
`;

const slideIn = keyframes`
  0% { transform: translateY(100%) scale(0.5); opacity: 0; }
  100% { transform: translateY(0%) scale(1); opacity: 1; }
`;

const fadeOut = keyframes`
  from { opacity: 1; }
  to { opacity: 0; }
`;

const slideOut = keyframes`
  0% { transform: translateY(0%) scale(1); opacity: 1; }
  100% { transform: translateY(100%) scale(0.5); opacity: 0; }
`;

const appearAndShrink = keyframes`
  0% {
    opacity: 0;
    transform: scale(1.3); 
    filter: blur(10px);
  }
  50% {
    opacity: 0.5;
    transform: scale(1.1);
    filter: blur(2px);
  }
  100% {
    opacity: 1;
    transform: scale(1); 
    filter: blur(0);
  }
`;

const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  backdrop-filter: blur(3px);
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: ${(props) => (props.isClosing ? fadeOut : fadeIn)} 0.3s ease-out
    forwards;
`;

const ModalContent = styled.div`
  background: #093500;
  color: #2eb813;
  padding: 20px;
  border-radius: 15px;
  width: 95%;
  max-width: 550px;
  max-height: 85vh;
  border: 2px solid #2eb813;
  position: relative;
  display: flex;
  flex-direction: column;
  animation: ${(props) =>
    props.isClosing
      ? css`
          ${slideOut} 0.3s ease-in forwards
        `
      : css`
          ${slideIn} 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards
        `};
`;

const ScrollContainer = styled.div`
  overflow-y: auto;
  padding-right: 10px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  &::-webkit-scrollbar {
    width: 6px;
  }
  &::-webkit-scrollbar-track {
    background: #051a00;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb {
    background: #2eb813;
    border-radius: 10px;
  }
  scrollbar-width: thin;
  scrollbar-color: #2eb813 #051a00;
`;

const CategorySection = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const CategoryTitle = styled.div`
  font-size: 14px;
  letter-spacing: 1.5px;
  color: #a2ff6c;
  border-bottom: 1px solid rgba(162, 255, 108, 0.3);
  padding-bottom: 5px;
  margin-bottom: 5px;
  font-weight: 900;
  opacity: 0;
  animation: ${appearAndShrink} 0.6s ease-out forwards;
  ${({ $delay }) => css`
    animation-delay: ${$delay || "0.2s"};
  `}
`;
const AchivmentItem = styled.div`
  display: flex;
  align-items: center;
  background: rgba(162, 255, 108, 0.05);
  border-radius: 12px;
  padding: 12px;
  gap: 15px;
  border: ${(props) =>
    props.isSpecial ? "2px solid #ff0000" : "1px solid #a2ff6c"};
  transition: transform 0.2s;
  opacity: 0;
  animation: ${appearAndShrink} 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    forwards;
  ${({ $index }) => css`
    animation-delay: ${0.3 + $index * 0.1}s;
  `}

  &:hover {
    background: rgba(162, 255, 108, 0.1);
    transform: translateX(3px);
  }
`;

const AchivmentImagePlace = styled.img`
  width: 50px;
  height: 50px;
  border-radius: 8px;
  object-fit: cover;
  border: 1px solid #2eb813;
  background: #051a00;
`;

const AchivmentName = styled.h3`
  margin: 0;
  font-size: 15px;
  color: #ffb36c;
`;

const AchivmentGoal = styled.p`
  margin: 3px 0 0;
  font-size: 11px;
  line-height: 1.3;
  opacity: 0.8;
  color: ${(props) => (props.isBlue ? "#00a2ff" : "#a2ff6c")};
`;

const RewardField = styled.div`
  width: 60px;
  height: 35px;
  background: rgba(46, 184, 19, 0.2);
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 13px;
  border: 1px dashed #ffb36c;
  color: #ffb36c;
  flex-shrink: 0;
`;

const CloseButton = styled.button`
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 28px;
  cursor: pointer;
  color: #2eb813;
  z-index: 10;
  &:hover {
    color: #a2ff6c;
    transform: rotate(90deg);
  }
`;

