import React, { useState } from "react";
import styled, { keyframes, css } from "styled-components";
//  
import turkeys from "../../photos/vip-images/collectors-edition.jpg";
import dinofroz from "../../photos/vip-images/vip-dinofroz.webp";
import monody from "../../photos/vip-images/vip-forest.webp";
import dragons from "../../photos/vip-images/vip-dragons.jpg";
import vip from "../../photos/hero-header/vip.jpg";
import music from "../../photos/vip-modal/music.jpg";
import time from "../../photos/vip-images/mechannic.jpg";
import ultra from "../../photos/vip-modal/realultra.jpg";
import clip from "../../photos/vip-images/clip.png";
import stars from "../../photos/vip-images/stars.jpg";
import lebid from "../../photos/vip-images/vip-lebid.jpg";
import buton from "../../photos/vip-modal/buton.jpg";
import texts from "../../photos/vip-modal/texts.jpg";
import horrordog from "../../photos/vip-images/horror.jpg";
import asium from "../../photos/vip-images/asium.jpg";
import rainbow from "../../photos/fan-art/rainbow.webp";
import letters from "../../photos/fan-art/letters.webp";

const appearAndShrink = keyframes`
  0% { opacity: 0; transform: scale(1.3); filter: blur(10px); }
  50% { opacity: 0.5; transform: scale(1.1); filter: blur(2px); }
  100% { opacity: 1; transform: scale(1); filter: blur(0); }
`;

const slideIn = keyframes`
  0% { transform: translateY(100%) scale(0.5); opacity: 0; }
  100% { transform: translateY(0%) scale(1); opacity: 1; }
`;

const slideOut = keyframes`
  0% { transform: translateY(0%) scale(1); opacity: 1; }
  100% { transform: translateY(100%) scale(0.5); opacity: 0; }
`;

const flowPlus = keyframes`
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
`;

const Overlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  backdrop-filter: blur(3px);
  background-color: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 5px;
`;

const VipModalDiv = styled.div`
  background-color: #3e2723;
  color: #fff;
  width: 98%;
  max-width: 950px;
  max-height: 95vh;
  padding: 15px;
  border-radius: 12px;
  position: relative;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  border: 2px solid ${(props) => (props.$isUltra ? "#00ff00" : "#ffb36c")};
  overflow-y: auto;
  transition: border-color 0.5s ease;
  animation: ${(props) => (props.$isClosing ? slideOut : slideIn)} 0.5s ease-out
    forwards;

  @media (max-width: 480px) {
    padding: 10px;
    padding-top: 35px;
  }
`;

const CloseButton = styled.button`
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  color: #ffb36c;
  font-size: 22px;
  cursor: pointer;
  z-index: 1010;
  &:hover {
    color: #fff;
  }
`;

const HeaderToggle = styled.button`
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  text-align: left;
  display: block;
  width: fit-content;
  outline: none;
  transition: transform 0.2s;
  &:hover {
    transform: scale(1.02);
  }
`;

const AnimatedText = styled.h1`
  font-family: "Inter", sans-serif;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 8px;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  ${(props) =>
    props.$variant === "ultra"
      ? css`
          background-image: linear-gradient(
            270deg,
            #ff7eb3,
            #ff758c,
            #7afcff,
            #feffb7,
            #58e2c2
          );
          background-size: 400% 400%;
          animation:
            ${flowPlus} 5s ease infinite,
            ${appearAndShrink} 0.8s ease-out forwards;
        `
      : css`
          background-image: linear-gradient(
            45deg,
            #ff0000,
            #ff7f00,
            #ffff00,
            #00ff00,
            #0000ff,
            #8b00ff
          );
          background-size: 100% 100%;
          animation: none !important;
          opacity: 1 !important;
          transform: scale(1) !important;
          filter: blur(0) !important;
        `}

  @media (max-width: 768px) {
    font-size: 18px;
  }
`;

const SwitchBackText = styled.div`
  font-size: 10px;
  color: #7afcff;
  cursor: pointer;
  text-decoration: underline;
  margin-top: -5px;
  margin-bottom: 10px;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
`;

const VipBlock = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 15px;
  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
  }
`;

const VipFixScroll = styled.div`
  flex: 1;
  height: 420px;
  min-width: 280px;
  overflow-y: auto;
  padding-right: 8px;
  @media (max-width: 768px) {
    width: 100%;
    height: 300px;
  }
  &::-webkit-scrollbar {
    width: 4px;
  }
  &::-webkit-scrollbar-thumb {
    background: #ffb36c;
    border-radius: 10px;
  }
`;

const BenefitCard = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  background: rgba(255, 179, 108, 0.05);
  border: 1px solid rgba(255, 179, 108, 0.15);
  border-radius: 8px;
  padding: 6px;
  opacity: 0;
  animation: ${appearAndShrink} 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
  margin-bottom: 6px;
  transition: transform 1s;
  ${({ $index }) => css`
    animation-delay: ${0.1 + $index * 0.05}s;
  `}
  &:hover {
    border-color: #ffb36c;
    transform: translateX(3px);
  }
`;

const BenefitImage = styled.img`
  width: 45px;
  height: 35px;
  border-radius: 4px;
  object-fit: cover;
  flex-shrink: 0;
`;

const VipBonus = styled.div`
  font-size: 9.9px;
  line-height: 1.3;
  color: #ffb36c;
  flex: 1;
`;

const SectionTitle = styled.div`
  font-weight: bold;
  color: #ffb36c;
  margin: 10px 0 6px 0;
  text-transform: uppercase;
  font-size: 12px;
  border-left: 2px solid #ffb36c;
  padding-left: 8px;
  opacity: 0;
  animation: ${appearAndShrink} 0.6s ease-out forwards;
  animation-delay: ${(props) => props.$delay || "0.2s"};
`;

const VipImage = styled.img`
  width: 260px;
  border-radius: 6px;
  border: 1px solid #ffb36c;
  @media (max-width: 768px) {
    width: 100%;
  }
`;

const VipButton = styled.button`
  width: 260px;
  border: 1px solid #ffb36c;
  cursor: pointer;
  padding: 8px;
  background: transparent;
  color: #ffb36c;
  font-size: 14px;
  font-weight: bold;
  border-radius: 4px;
  &:hover {
    background: #ffb36c;
    color: #3e2723;
  }
  @media (max-width: 768px) {
    width: 100%;
  }
`;

const VipText = styled.p`
  width: 100%;
  max-width: 300px;
  font-size: 12px;
  display: flex;
  justify-content: flex-end;
  color: #ffb36c;
`;

const RedLine = styled.div`
  background: red;
  width: 100%;
  height: 1px;
  margin: 10px 0;
`;

const VipWarning = styled.p`
  color: red;
  font-size: 12px;
  word-wrap: break-word;
  margin-bottom: 2px;
`;
const VipModal = ({ onClose }) => {
  const [isClosing, setIsClosing] = useState(false);
  const [tier, setTier] = useState("plus");
  const [showContent, setShowContent] = useState(true);

  const handleClose = (e) => {
    if (e) e.stopPropagation();
    setIsClosing(true);
    setTimeout(() => {
      onClose();
    }, 500);
  };

  const handleTierSwitch = (newTier) => {
    if (tier === newTier) return;
    setShowContent(false);
    setTimeout(() => {
      setTier(newTier);
      setShowContent(true);
    }, 100);
  };

  const plusBenefits = {
    ai: [
      {
        src: texts,
        text: "6  / +   4 ,  3  / +   2.      0,11,  0,13 + .          .",
      },
      { src: lebid, text: "2 ( 1) /  3,99,  5,99." },
      { src: clip, text: " 65/ ( 75/)." },
      { src: music, text: "mp3 3/,  5/." },
    ],
    music: [
      {
        src: monody,
        text: "     Monody-TheFatRat  VIP- ( )   3.  ,   7.",
      },
      {
        src: asium,
        text: "    8 ,   4!   50% (5,  2  ,  3( 1)  )!  (, , )",
      },
      { src: dinofroz, text: "   ." },
      { src: horrordog, text: "   . ." },
      {
        src: letters,
        text: "  ,       20-30,  20-40.",
      },
      {
        src: dragons,
        text: "     /   25%  .      : 20-30,  20-40.",
      },
    ],
    system: [
      {
        src: vip,
        text: "   (   ,   ).",
      },
      { src: stars, text: "   ." },
      {
        src: dragons,
        text: " 25%   ,     20,   -   < 40.    40. ,           .   ()     50.    *2.   2500,  2000.  25%( 20%)  (   10).",
      },
      {
        src: buton,
        text: "  /,     12  1.    4.        .",
      },
      {
        src: rainbow,
        text: " '     3,   7  .",
      },
      { src: time, text: "  ' ." },
    ],
  };

  const ultraBenefits = {
    ai: [
      {
        src: texts,
        text: "12  / +   8 .   0,1 (   )",
      },
      { src: lebid, text: "3 /  2,99,  4,99." },
      { src: clip, text: " 45/." },
      { src: music, text: "mp3 2/." },
    ],
    music: [
      {
        src: monody,
        text: "     Monody-TheFatRat  VIP- ( )    .",
      },
      {
        src: asium,
        text: "    12 .   50% (8  ,  4  )!",
      },
      { src: horrordog, text: " 10 /  . ." },
      {
        src: letters,
        text: "  ,       20-25",
      },
      {
        src: dragons,
        text: "     /   50%  .      : 20-30,  20-40.",
      },
    ],
    system: [
      { src: vip, text: "   (  )." },
      {
        src: dragons,
        text: " 50%   .   ()     100.    *3.   5000.  50%  (   20).",
      },
      {
        src: buton,
        text: "  /,     3.    8.",
      },
      { src: rainbow, text: " '    ." },
      { src: time, text: "  ' ." },
    ],
  };

  const current = tier === "plus" ? plusBenefits : ultraBenefits;

  return (
    <Overlay onClick={handleClose}>
      <VipModalDiv
        $isClosing={isClosing}
        $isUltra={tier === "ultra"}
        onClick={(e) => e.stopPropagation()}
      >
        <CloseButton onClick={handleClose}>&times;</CloseButton>

        <HeaderToggle
          onClick={() => handleTierSwitch(tier === "plus" ? "ultra" : "plus")}
        >
          <AnimatedText $variant={tier} key={`title-${tier}`}>
            {tier === "plus" ? "+" : "+ "}
          </AnimatedText>
        </HeaderToggle>

        {tier === "ultra" && (
          <SwitchBackText onClick={() => handleTierSwitch("plus")}>
               +
          </SwitchBackText>
        )}

        <VipBlock>
          <VipFixScroll key={`scroll-area-${tier}`}>
            {showContent && (
              <>
                <SectionTitle $delay="0.1s"></SectionTitle>
                {current.ai.map((item, i) => (
                  <BenefitCard key={`ai-${tier}-${i}`} $index={i}>
                    <BenefitImage src={item.src} />
                    <VipBonus>{item.text}</VipBonus>
                  </BenefitCard>
                ))}

                <SectionTitle $delay="0.3s">  </SectionTitle>
                {current.music.map((item, i) => (
                  <BenefitCard key={`mu-${tier}-${i}`} $index={i + 4}>
                    <BenefitImage src={item.src} />
                    <VipBonus>{item.text}</VipBonus>
                  </BenefitCard>
                ))}

                <SectionTitle $delay="0.5s"></SectionTitle>
                {current.system.map((item, i) => (
                  <BenefitCard key={`sy-${tier}-${i}`} $index={i + 10}>
                    <BenefitImage src={item.src} />
                    <VipBonus>{item.text}</VipBonus>
                  </BenefitCard>
                ))}
              </>
            )}
          </VipFixScroll>

          <div style={{ display: "flex", gap: "6px", flexDirection: "column" }}>
            <SectionTitle $delay="0.4s">
              {tier === "plus"
                ? "TurkeyStudio VIP!"
                : "TurkeyStudio Ultra VIP!"}
            </SectionTitle>
            <VipImage
              src={tier === "plus" ? turkeys : ultra}
              key={`img-${tier}`}
            />

            {tier === "plus" ? (
              <>
                <VipButton>1,99/</VipButton>
                <VipButton> : 3,99/</VipButton>
              </>
            ) : (
              <>
                <VipButton>5,99/</VipButton>
                <VipButton> : 7,99/</VipButton>
              </>
            )}
            <VipText>
                ,   .
            </VipText>
          </div>
        </VipBlock>

        <RedLine />
        <VipWarning>
          : 1.  ,   
           ,       .
        </VipWarning>
        <VipWarning>
          : 2.   ,    
          .     ( ) .
        </VipWarning>
        <VipWarning>
          : 3. +   + ,   
             + (,      +.)
        </VipWarning>
      </VipModalDiv>
    </Overlay>
  );
};

export default VipModal;
