import React, { useState } from "react";
import styled, { keyframes, css } from "styled-components";

const slideIn = keyframes`
  0% { transform: translateY(100%) scale(0.9); opacity: 0; }
  100% { transform: translateY(0%) scale(1); opacity: 1; }
`;

const slideOut = keyframes`
  0% { transform: translateY(0%) scale(1); opacity: 1; }
  100% { transform: translateY(100%) scale(0.9); opacity: 0; }
`;

const appearAndShrink = keyframes`
  0% {
    opacity: 0;
    transform: scale(1.3); 
    filter: blur(10px);
  }
  50% {
    opacity: 0.5;
    transform: scale(1.1);
    filter: blur(2px);
  }
  100% {
    opacity: 1;
    transform: scale(1); 
    filter: blur(0);
  }
`;

const animatedStyle = css`
  opacity: 0;
  transform-origin: left center; /*       */
  animation: ${appearAndShrink} 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94)
    forwards;
  ${({ $index }) => css`
    animation-delay: ${0.2 + ($index || 0) * 0.08}s;
  `}
`;

const Overlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3000;
  transition: opacity 0.4s ease;
  opacity: ${(props) => (props.$isClosing ? 0 : 1)};
  backdrop-filter: blur(5px);
`;

const Content = styled.div`
  background: #ffd001;
  padding: 40px;
  border-radius: 20px;
  max-width: 900px;
  width: 95%;
  position: relative;
  font-family:
    "Inter",
    -apple-system,
    sans-serif;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  animation: ${(props) => (props.$isClosing ? slideOut : slideIn)} 0.4s
    cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: #999;
  }
`;

const CloseBtn = styled.button`
  position: absolute;
  top: 20px;
  right: 20px;
  background: transparent;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 34px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  &:hover {
    transform: rotate(90deg);
  }
`;

const Section = styled.section`
  margin-bottom: 25px;
`;

const SectionTitle = styled.h3`
  font-size: 16px;
  color: #111;
  margin-top: 30px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 10px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  border-left: 4px solid #8a2be2;
  padding-left: 15px;
  ${animatedStyle}
`;

const Text = styled.div`
  font-size: 14px;
  line-height: 1.7;
  color: #555;
  margin-bottom: 10px;
  ${animatedStyle}
`;

const HighlightBox = styled.div`
  background: #acacac;
  border: 1px dashed #8a2be2;
  padding: 15px;
  border-radius: 10px;
  margin: 15px 0;
  ${animatedStyle}
`;
const AcceptBtn = styled.button`
  margin-top: 20px;
  padding: 12px 35px;
  background: #8a2be2;
  color: white;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  box-shadow: 0 4px 15px rgba(138, 43, 226, 0.3);
  opacity: 0;
  animation: ${appearAndShrink} 0.6s forwards;
  animation-delay: 3s;
`;

